<template>
  <div v-if="current" style="font-size: 30px;width: 100%;height: 100%"  @touchstart="show=true" @touchend="show=false" >


    <br>
    <katex :math="current.a"></katex>
    <br>
    <katex v-if="show" :math="current.b"></katex>
    <button @click.prevent="rand" @touchend.stop @touchstart.stop  style="width: 100%;height: 50px;bottom: 50px;left: 0;position: fixed">换一个</button>
    <label @touchend.stop @touchstart.stop style="width: 100%;height: 50px;bottom: 0;left: 0;position: fixed">乘法：<input type="checkbox" v-model="cf"></label>

  </div>
</template>
<script>
import Katex from "@/components/Katex";

export default {
  components: {Katex},
  data:()=>{

    return{
      cf:false,
      current:null,
      show:false,
    }
  },
  mounted() {
    this.rand()
  },
  methods:{

    rand(){
      this.show=false
      let a=Math.floor(Math.random()*100)
      let b=Math.floor(Math.random()*100)
      let ops=['+','-','\\times ','\\div']
      let op;
      if (this.cf){
        op=Math.floor(Math.random()*(ops.length-1))
      }else {
        op=Math.floor(Math.random()*(ops.length-2))
      }

      let result
      switch (op) {
        case 0:
          result= a+b;
          break;
        case 1:
          result= a-b;
          break;
        case 2:
          result= a*b;
          break;

      }
      this.current= {
        a: a+ops[op]+b,
        b: result.toString()
      }
    },
  }
}
</script>
